.about {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  outline: none;
  padding: 8vh;
}

.about__message {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.about__error {
  color: #00dd1d;
  font-size: calc((40 / 16) * 1rem);
  font-weight: 400;
}

.about__text {
  font-size: calc((40 / 16) * 1rem);
}

.about__button {
  --buttonFontSize: calc((40 / 16) * 1rem);
  margin-top: 4vh;
}
